body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  color: #333; }

body {
  height: 100%;
  width: 100%;
  font-family: 'Open Sans', sans-serif;
  background-color: #ebebeb; }

.bold {
  font-weight: bold; }

.clear {
  clear: both; }

.calculator {
  max-width: 450px;
  position: relative;
  margin: 0 auto; }

.logo {
  margin: 25px 0px;
  text-align: center; }

.logo i {
  display: block;
  height: 140px;
  background-image: url(/pokemongoxp/static/media/pokemon-go-logo-transparent.557fcc0f.png);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
  color: rgba(0, 0, 0, 0); }

label {
  font-size: 18px; }

input {
  font-size: 16px;
  padding: 0px 10px;
  height: 40px;
  width: 100%;
  border: 1px solid #888;
  border-radius: 2px;
  outline: none; }

input:focus {
  border-color: #ee1515; }

.form-div {
  margin: 20px; }
  .form-div label {
    display: block;
    margin: 0px 0px 5px; }
  .form-div.button input {
    border-color: #ee1515;
    background-color: #ee1515;
    color: #FFF;
    height: 50px;
    line-height: 50px;
    font-size: 18px;
    cursor: pointer; }
    .form-div.button input:hover {
      box-shadow: inset 0px 0px 500px rgba(0, 0, 0, 0.15); }
    .form-div.button input:active {
      box-shadow: inset 0px 0px 500px rgba(0, 0, 0, 0.3); }

.language-option {
  float: right;
  margin: 0px 20px 20px 0px; }
  .language-option p {
    padding: 0px 10px;
    display: inline-block; }
  .language-option select {
    font-size: 16px;
    display: inline-block;
    height: 40px; }

.result {
  max-width: 800px;
  position: relative;
  margin: 0 auto; }
  .result .back, .result .raw-data-button {
    display: inline-block;
    width: 50%;
    padding: 15px; }
  .result .raw-data-button {
    text-align: right; }
  .result .intro-text {
    margin: 10px 0px; }

.results-ul-header {
  position: relative;
  background-color: #ee1515;
  border: 1px solid #222224; }

.results-ul-header .first, .results-ul li .first {
  border: 1px solid #222224;
  border-width: 0px 1px 0px 0px;
  position: absolute;
  width: 40px;
  line-height: 40px;
  text-align: center; }

.results-ul-header .second, .results-ul li .second {
  position: relative;
  margin: 0px 0px 0px 50px;
  line-height: 40px; }

.results-ul-header .first, .results-ul-header .second {
  color: #FFF; }

.results-ul {
  position: relative; }

.results-ul li {
  background-color: #FFF;
  position: relative;
  border: 1px solid #222224;
  border-width: 0px 1px 1px 1px;
  overflow: hidden; }

.results-ul li .third {
  position: absolute;
  padding: 8px 5px;
  right: 0px;
  top: 0px;
  height: 40px;
  text-align: center;
  min-width: 80px; }
  .results-ul li .third .redButton {
    padding: 0px 10px; }

.results-ul li .rewards {
  margin: 0px 0px 0px 39px;
  border: 1px solid #222224;
  border-width: 0px 0px 0px 1px;
  transition: all 0.2s;
  max-height: 0px;
  padding: 0px 10px; }
  .results-ul li .rewards.open {
    max-height: 120px;
    padding-top: 10px;
    padding-bottom: 10px; }

.redButton {
  cursor: pointer;
  -moz-user-select: -moz-none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-size: 18px;
  width: 100%;
  outline: none;
  border: 1px solid;
  color: #FFF;
  border-color: #f00000;
  border-radius: 2px;
  margin: 0px;
  height: 24px;
  font-size: 14px;
  line-height: 24px;
  background-color: rgba(238, 21, 21, 0.9); }

.redButton:hover {
  box-shadow: inset 0px 0px 500px rgba(0, 0, 0, 0.15); }

.redButton:active {
  box-shadow: inset 0px 0px 500px rgba(0, 0, 0, 0.3); }

.results-footer-div {
  font-size: 11px;
  margin: 0px 0px 15px 0px;
  padding: 15px; }

.results-share-div {
  padding: 20px;
  overflow: hidden;
  height: 60px; }
  .results-share-div .fb-share-button {
    display: inline-block;
    width: 100px; }
    .results-share-div .fb-share-button iframe {
      border: 0px;
      width: 100%; }
  .results-share-div .twitter-share-button {
    padding: 0px 10px;
    height: 28px;
    line-height: 28px;
    position: absolute;
    display: inline-block;
    border: 0;
    border-radius: 3px;
    text-decoration: none;
    font-size: 13px;
    font-weight: 700;
    background-color: #1da1f2;
    color: #FFF; }

table {
  border-collapse: collapse; }

table, th, td {
  border: 1px solid #999; }

.rawdata-table th {
  text-align: left;
  padding: 3px 6px;
  background-color: rgba(255, 0, 0, 0.8);
  color: #FFF; }

.rawdata-table td {
  padding: 3px 6px; }

.not-found {
  text-align: center;
  line-height: 100px; }

